import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardHeader, Col, Label, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, Tab, TabGroup, WindowComponent } from 'summer';
import { CLASS_NAME, LucroPrejuizoMCG } from '../../../../models/dtos/mcg/lucroPrejuizoMCG';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface LucroPrejuizoEditViewProps extends EditViewProps<LucroPrejuizoMCG> {}

const LucroPrejuizoEditView: FC<LucroPrejuizoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const formProps = useForm<LucroPrejuizoMCG>({
    defaultValues: { empresa: globalReducer.globalParameter.empresa, ...selectedItem },
    mode: 'onBlur',
  });

  const [totalRecursos, setTotalRecursos] = useState('');
  const [totalAplicacoes, setTotalAplicacoes] = useState('');
  const [totalAcumulado, setTotalAcumulado] = useState('');

  useEffect(
    () => {
      if (formProps.getValues().id) {
        somaRecursos();
        somaAplicacoes();
      } else {
        setTotalRecursos('0');
        setTotalAplicacoes('0');
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(() => {
    lucroPrejuizoAcumulado();
  });

  const somaRecursos = () => {
    setTotalRecursos(
      (
        +formProps.getValues().saldoAnteriorLucroAcumulado +
        +formProps.getValues().ajusteCredorPeriodoBaseAnterior +
        +formProps.getValues().correcaoMonetariaLucroAcumulado +
        +formProps.getValues().reversaoReserva +
        +formProps.getValues().outroRecurso +
        +formProps.getValues().lucroLiquidoPeriodoBaseProvIR -
        (+formProps.getValues().saldoAnteriorPrejuizoAcumulado +
          +formProps.getValues().ajusteDevedorPeridoBaseAnterior +
          +formProps.getValues().correcMonetariaPrejuizoAcumulado +
          +formProps.getValues().prejuizoLiquidoPeriodoBaseProvIR)
      ).toFixed(2)
    );
  };

  const somaAplicacoes = () => {
    setTotalAplicacoes(
      (
        +formProps.getValues().transferenciaReserva +
        +formProps.getValues().divLucroDistribuidoPagoCreditado +
        +formProps.getValues().impostoLucroLiquido +
        +formProps.getValues().parcelaLucroIncorporadaCapital +
        +formProps.getValues().outraAplicacao
      ).toFixed(2)
    );
  };

  const [estiloAcumulado, setEstiloAcumulado] = useState('');

  const lucroPrejuizoAcumulado = () => {
    if (+totalRecursos - +totalAplicacoes < 0) {
      setEstiloAcumulado('red');
    } else {
      setEstiloAcumulado('black');
    }
    setTotalAcumulado((+totalRecursos - +totalAplicacoes).toFixed(2));
  };

  return (
    <WindowComponent title="Manutenção de Lucros/Prejuízos">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Card>
          <CardBody>
            <TabGroup formProps={formProps}>
              <Tab title="Recursos">
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="saldoAnteriorLucroAcumulado"
                      isPrependLabel={true}
                      label="Saldo Anterior de Lucros Acumulados"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="ajusteCredorPeriodoBaseAnterior"
                      isPrependLabel={true}
                      label="Ajustes Credores de Períodos Base Anteriores"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="correcaoMonetariaLucroAcumulado"
                      isPrependLabel={true}
                      label="Correção Monetária dos Lucros Acumulados"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="reversaoReserva"
                      isPrependLabel={true}
                      label="Reversão de Reservas"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="outroRecurso"
                      isPrependLabel={true}
                      label="Outros Recursos"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="lucroLiquidoPeriodoBaseProvIR"
                      isPrependLabel={true}
                      label="Lucro Líquido do Período Base Depois Prov. p/IR"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <hr />
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="saldoAnteriorPrejuizoAcumulado"
                      isPrependLabel={true}
                      label="Saldo Anterior de Prejuízos Acumulados"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="ajusteDevedorPeridoBaseAnterior"
                      isPrependLabel={true}
                      label="Ajuste Devedores de Período Base Anteriores"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="correcMonetariaPrejuizoAcumulado"
                      isPrependLabel={true}
                      label="Correção Monetária dos Prejuízos Acumulados"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="prejuizoLiquidoPeriodoBaseProvIR"
                      isPrependLabel={true}
                      label="Prejuízos Líquidos do Período Base Depois Prov. p/IR"
                      type={InputType.NUMBER}
                      onBlur={somaRecursos}
                    />
                  </Col>
                </Row>
              </Tab>
              <Tab title="Aplicações">
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="transferenciaReserva"
                      isPrependLabel={true}
                      label="Transferência de Reservas"
                      type={InputType.NUMBER}
                      onBlur={somaAplicacoes}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="divLucroDistribuidoPagoCreditado"
                      isPrependLabel={true}
                      label="Div. Lucros ou Distribuídos, Pagos, Creditados"
                      type={InputType.NUMBER}
                      onBlur={somaAplicacoes}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="impostoLucroLiquido"
                      isPrependLabel={true}
                      label="Imposto Sobre Lucro Líquido - Art.35 Lei 7713/8"
                      type={InputType.NUMBER}
                      onBlur={somaAplicacoes}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="parcelaLucroIncorporadaCapital"
                      isPrependLabel={true}
                      label="Parcelas dos Lucros Incorporadas ao Capital"
                      type={InputType.NUMBER}
                      onBlur={somaAplicacoes}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 13 } }}
                      id="outraAplicacao"
                      isPrependLabel={true}
                      label="Outras Aplicações"
                      type={InputType.NUMBER}
                      onBlur={somaAplicacoes}
                    />
                  </Col>
                </Row>
              </Tab>
            </TabGroup>
          </CardBody>
        </Card>
        <br />
        <br />
        <Card>
          <CardHeader className="font-weight-bold bg-light justify-content-center">Totalizadores</CardHeader>
          <CardBody>
            <Row>
              <Col>
                <Label>Soma dos Recursos: </Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <Label style={{ fontWeight: 'bold' }}>{totalRecursos} </Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>Soma das Aplicações: </Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <Label style={{ fontWeight: 'bold' }}>{totalAplicacoes} </Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>Lucros/Prejuízos Acumulados: </Label>
              </Col>
            </Row>
            <Row>
              <Col>
                <Label style={{ color: estiloAcumulado, fontWeight: 'bold' }}>{totalAcumulado} </Label>
              </Col>
            </Row>
          </CardBody>
        </Card>
      </EditForm>
    </WindowComponent>
  );
};

export default LucroPrejuizoEditView;
